.App {
  text-align: center;
  background-color: #a7a7a7;
  margin-top: -22px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.level0 .hexagon {
  fill: #008200;
}

.level1 .hexagon {
  fill: #0000c2;
}

.level2 .hexagon {
  fill: #c20000;
}

.hexagon text {
  fill: black;
  user-select: none;
  stroke: none;
}

.winner .hexagon {
  stroke: gold;
}
